import $ from 'jquery'
import fetch from 'node-fetch'
let Parser = require('rss-parser');
let parser = new Parser();
import fetchJsonp from 'fetch-jsonp'
const starred = $('.starred')
const lastrepos = $('.lastrepos')
const news = $('.news')
const tools = $('.tools')
const contact = $('.contact')
const about = $('.about')
const programming = $('.programming')

async function getStarred() {
  let res = await fetch('https://api.github.com/users/injecti0n/starred')
  let data = res.json()
  return data
}



async function getRepos() {
  let res = await fetch('https://api.github.com/users/injecti0n/repos?per_page=1000')
  let data = res.json()
  return data
}


async function getBlogNews() {

  let res = await fetchJsonp('https://www.hack4.net/feeds/posts/default?alt=json', { "mode": "no-cors" })
  let data = res.json()
  return data
}


getBlogNews().then(res => {
  //console.log(res.feed.entry)
  // console.log(res.feed.entry) // title
  res.feed.entry.forEach(news => {
    // ${news.link[3].href}
    // console.log(news)

    document.querySelector('.newslist').innerHTML += `
   <li class="feed-item">
      <span class="text hack4netNews">${news.title.$t} <a href="${news.link[3].href}" target="_blank">${news.title.$t}</a></span>
   </li>
   `
    // news.published timeeeee
  })
  // console.log(res)
})


getStarred().then(res => {
  // console.log(res)
  res.forEach(repo => {
    document.querySelector('.starredlist').innerHTML += `
    <li class="feed-item">
                <span class="text">${repo.description} <a href="${repo.html_url}" target="_blank">${repo.html_url}</a></span>
              </li>
    `
  })

})

getRepos().then(res => {
  // console.log(res)
  res.forEach(repo => {
    document.querySelector('.repos').innerHTML += `
    <li class="feed-item">
                <span class="text">${repo.description} <a href="${repo.html_url}" target="_blank">${repo.html_url}</a></span>
              </li>
    `
  })

})


function makeWindow(window_title, html_mount) {
  return new WinBox(window_title, {
    mount: document.getElementById(html_mount)
      .cloneNode(true),
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)'
  });
}

function makeWindowIframe(window_title, url) {
  return new WinBox(window_title, {
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)',
    url: url
  });
}

function makeWindowWithStatic(window_title, content) {
  return new WinBox(window_title, {
    x: "center",
    y: "center",
    background: 'rgb(54 69 68)',
    html: `<p class="lead">${content}</p>`
  });
}

